* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    background-color: #f5f5f5;
    color: #ffffff;
}

.bg-video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.wrapper {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px);
    animation: blur 2s ease-in-out;
    z-index: 2;
}

.neon-link-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 25px;
}

.neon-link {
    font-weight: 500;
    position: relative;
    padding: 15px 60px;
    color: #ffffff;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 20px;
    overflow: hidden;
    transition: 0.2s;
    text-decoration: none;
    display: inline-block;
    border-radius: 5px;
    border: 2px solid rgba(255, 255, 255, 0.19);
}

.neon-link:hover {
    color: #255784;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.8),
    0 0 40px rgba(255, 255, 255, 0.8),
    0 0 80px rgba(255, 255, 255, 0.8);
    transition-delay: 0.5s;
}

.neon-link span {
    position: absolute;
    display: block;
}

.neon-link span:nth-child(1) {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, #2196f3);
}

.neon-link:hover span:nth-child(1) {
    left: 100%;
    transition: 0.5s;
}

.neon-link span:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, #2196f3);
}

.neon-link:hover span:nth-child(3) {
    right: 100%;
    transition: 0.5s;
    transition-delay: 0.25s;
}

.neon-link span:nth-child(2) {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #2196f3);
}

.neon-link:hover span:nth-child(2) {
    top: 100%;
    transition: 0.5s;
    transition-delay: 0.125s;
}

.neon-link span:nth-child(4) {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, #2196f3);
}

.neon-link:hover span:nth-child(4) {
    bottom: 100%;
    transition: 0.5s;
    transition-delay: 0.375s;
}


.camera-container {
    width: 500px;
    height: 500px;
    overflow: hidden;
    position: relative;
    border-radius: 50%;
    background: url("./assets/login.gif") no-repeat center center/cover;
    box-shadow: 0 0 50px rgba(255, 255, 255, 0.2);
}

.face-overlay {
    position: absolute;
    top: 40px;
    right: 60px;
    bottom: 40px;
    left: 60px;
    box-shadow: 0 0 20px 156px rgb(0 0 0 / 60%);
    border: 8px dashed #ddd;
    border-radius: 50%;
}

.face-message {
    font-size: 1.5rem;
    margin-bottom: 20px;
}


#webcam {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
}

.preview {
    position: relative;
}

.preview canvas {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
}

.preview img {
    width: 500px;
    height: 500px;
    object-fit: cover;
    overflow: hidden;
    object-position: center;
    border-radius: 50%;
}

.preview::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, #ffffff, #023cfa);
    z-index: 40;
    animation: lineAnimate 0.5s linear alternate infinite;
}

.preview.stopAnimate::after {
    display: none;
    animation-play-state: paused;
}

.hidden {
    display: none !important;
}

.alert-danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}

.alert-danger hr {
    border-top-color: #f1b0b7;
}

.alert-danger .alert-link {
    color: #491217;
}

.alert.alert-danger li {
    list-style-type: disclosure-closed;
    padding-left: 5px;
    margin: 0 30px;
    font-size: 16px;
}

.alert-success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}

.alert-success hr {
    border-top-color: #b1dfbb;
}

.alert-success .alert-link {
    color: #0b2e13;
}

.login-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
}


@keyframes blur {
    from {
        backdrop-filter: blur(0px);
    }
    to {
        backdrop-filter: blur(5px);
    }
}

@keyframes load {
    0% {
        transform: rotate(0);
        filter: hue-rotate(0);
    }
    100% {
        transform: rotate(360deg);
        filter: hue-rotate(360deg);
    }
}

@keyframes lineAnimate {
    0% {
        top: 0;
        filter: hue-rotate(0);
    }
    100% {
        top: 100%;
        filter: hue-rotate(360deg);
    }
}